/* Styling for the calculator container */
.calculator {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 320px;
  margin: 50px auto;
  border: 2px solid #ccc;
  padding: 10px;
  border-radius: 10px;
}

/* Display screen styling */
#display {
  font-size: 2rem;
  background-color: #222;
  color: #fff;
  text-align: right;
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 15px;
}

/* Styling for the calculator buttons */
.buttons {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 10px;
  width: 100%;
}

/* General button styling */
button {
  padding: 20px;
  font-size: 1.5rem;
  cursor: pointer;
}

/* Clear button spans two columns */
button#clear {
  grid-column: span 2;
}

/* Zero button spans two columns */
button#zero {
  grid-column: span 2;
}
